import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Col, Spin, Upload, Tooltip, Icon } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/inventory/jobCosting/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class JobCostingDocUpdateForm extends Component {
  state = {
    fileList: [],
    //file : null,
    uploading: false
  };

  handleSubmit = e => {
    e.preventDefault();
    //console.log("");

    this.props.form.validateFieldsAndScroll((err, value) => {
      // console.log(err)
      if (!err) {
        this.handleUpload();
      }
    });
  };

  componentDidUpdate() {
    // Display error notif
    // if (this.props.customer.error) {
    //   notification("error", this.props.customer.error);
    //   this.props.clearError();
    // }

    if (this.props.jobCosting.success) {
      this.setState({
        uploading: false
      });
    }
  }

  handleUpload = () => {
    const { fileList } = this.state;
    const { uploadDocument } = this.props;
    let body;
    fileList.forEach(file => {
      // console.log(file);
      body = {
        document: file,
        description:
          this.props.form.getFieldValue("description") === undefined
            ? null
            : this.props.form.getFieldValue("description")
      };
    });

    // console.log(body)
    this.setState({
      uploading: true
    });

    uploadDocument(this.props.jobCosting.job_costing.id_job_costing, body);
  };

  handleDelete = id => {
    const { deleteDocument } = this.props;
    deleteDocument(id);
  };

  listAction = {
    title: "",
    dataIndex: "id_job_costing_document",
    rowKey: "id_job_costing_document",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip title={<IntlMessages id="download" />}>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href={
              localStorage.getItem("server") + row.document_path
            }
            download
          >
            <Button className="invoiceRstBtn" size="small">
              <i className="ion-android-download" />
            </Button>
          </a>
        </Tooltip>

        <Popconfirms
          title={<IntlMessages id="document.popconfirm.delete" />}
          onConfirm={this.handleDelete.bind(this, text)}
          okText={<IntlMessages id="yes" />}
          cancelText={<IntlMessages id="no" />}
        >
          <Tooltip title={<IntlMessages id="delete" />}>
            <Button className="invoiceDltBtn" size="small">
              <i className="ion-trash-b" />
            </Button>
          </Tooltip>
        </Popconfirms>
      </React.Fragment>
    )
  };

  render() {
    let columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "uploaded_date",
        rowKey: "uploaded_date",
        width: "25%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "fileDescription" }),
        dataIndex: "description",
        rowKey: "description",
        width: "50%",
        render: text => {
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "uploadedBy" }),
        dataIndex: "uploaded_by",
        rowKey: "uploaded_by",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      }
    ];

    // console.log(this.props.jobCosting.loading);
    const { getFieldDecorator } = this.props.form;
    const { jobCosting, downloadDocument } = this.props;
    const documents = jobCosting.job_costing
      ? jobCosting.job_costing.documents
      : [];
    const { loading } = jobCosting;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { fileList } = this.state;
    const props = {
      multiple: false,
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList
          };
        });
      },
      beforeUpload: file => {
        this.setState({
          fileList: [file]
        });
        return false;
      },
      fileList
    };

    if (
      permissions.includes("Upload Doc Job Costing") &&
      columns.length === 3
    ) {
      columns.push(this.listAction);
    }

    Object.keys(documents).map((document, index) => {
      return dataSource.push({
        ...documents[document],
        key: document
      });
    });

    return (
      <div>
        {this.props.jobCosting.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (
          <Form layout="vertical" onSubmit={this.handleSubmit}>
            <div style={{ padding: "0 20px 0 20px" }}>
              {permissions.includes("Upload Doc Job Costing") ? (
                <React.Fragment>
                  <Col span={8}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="fileDescription" />}
                    >
                      {getFieldDecorator("description", {
                        // initialValue: ,
                        rules: [
                          // {
                          //   required: true,
                          //   message: <IntlMessages id="required" />
                          // },
                          {
                            max: 30,
                            message: <IntlMessages id="maxLength30" />
                          }
                        ]
                      })(
                        <Input
                          // type="file"
                          //onChange={this.handleFile.bind(this)}
                          style={{ width: "100%", height: "35px" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={4} style={{ marginTop: "20px" }}>
                    <Upload {...props}>
                      <Button>
                        <Icon type="upload" />{" "}
                        {getLanguage({ id: "selectFile" })}
                      </Button>
                    </Upload>
                  </Col>
                  <Button
                    type="primary"
                    onClick={this.handleUpload}
                    disabled={fileList.length === 0}
                    loading={loading}
                    style={{ marginTop: "20px", marginLeft: "0%" }}
                  >
                    {loading
                      ? getLanguage({ id: "uploading" })
                      : getLanguage({ id: "upload" })}
                  </Button>
                </React.Fragment>
              ) : null}

              <Col span={24} style={{ marginTop: "20px" }}>
                <CardWrapper>
                  <div className="isoInvoiceTable">
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        // size="small"
                        dataSource={dataSource}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        pagination={true}
                      />
                    </Scrollbars>
                  </div>
                </CardWrapper>
              </Col>
            </div>
          </Form>
        )}
      </div>
    );
  }
}

const WrappedJobCostingDocUpdateForm = Form.create({
  name: "update_job_costing_document"
})(JobCostingDocUpdateForm);

//export default WrappedJobCostingDocUpdateForm;

const mapStateToProps = state => {
  return {
    jobCosting: state.jobCosting,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  WrappedJobCostingDocUpdateForm
);
