import React, { Component } from "react";
import moment from "moment"
import EditableTableExpense from "./editableTableExpense";
import EditableTableItem from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form, Input, Select, Col, DatePicker,
  Popconfirm, Steps, Row, Tabs, Divider, Spin, Empty
} from "antd";
import { customDot } from "../../../App/app.style";
import notification from "../../../../components/notification";
import ModalCancel from "./modalCancel";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const TabPane = Tabs.TabPane;
const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class JobCostingUpdateForm extends Component {
  state = {
    check: 1,
    temp_job_costing_detail: null,
    temp_job_costing_details: [],
    job_costing_details: [],
    temp_expense: null,
    temp_expenses: [],
    expenses: [],
    total_cost: 0,
    total_expense: 0,
    visibleModalJobCosting: false,
    search: null,
    id_location: null,
    clear: false,
    visibleModalCancel: false,
  }

  componentDidUpdate = () => {
    if (this.state.check === 1 && this.props.jobCosting.job_costing !== null) {
      const { job_costing, items } = this.props.jobCosting;
      const { setFieldsValue } = this.props.form;
      const { getItems } = this.props;


      setFieldsValue({
        id_location: job_costing.id_location,
        id_gl_account: job_costing.id_gl_account,
        note: job_costing.note,
      })

      getItems(job_costing.id_location)

      let job_costing_details = [];
      let expenses = [];
      let total_cost = 0;
      let total_expense = 0;


      job_costing.job_costing_details.map((data, key) => {

        const { current_item, status } = job_costing;
        // // Jika Import Pake Cost Per-Unit yang baru
        let item = status === 1 ? current_item.find(x => x.id_item === data.id_item) : { current_qty: 0 };
        //console.log(item)
        // console.log(item)

        let job_costing_detail = {
          index_no: data.index_no,
          id_item: data.id_item,
          item_code: data.item_code,
          item_name: data.item_name,
          base_qty: parseFloat(data.base_qty),
          current_qty: parseFloat(item.current_qty),
          id_base_unit: data.id_base_unit,
          base_unit_code: data.base_unit_code,
          cost_per_unit: parseFloat(data.cost_per_unit),
          sub_total: parseFloat(data.base_qty) * parseFloat(data.cost_per_unit),
          id_department: data.id_department,
          id_project: data.id_project,
        }
        job_costing_details.push(job_costing_detail);
        total_cost += parseFloat(data.amount)
      })

      job_costing.expenses.map((data, key) => {
        let expense = {
          index_no: data.index_no,
          id_account: data.id_account,
          account_code: data.account_code,
          account_name: data.account_name,
          amount: parseFloat(data.amount),
        }
        expenses.push(expense);
        total_expense += parseFloat(data.amount)
      })

      //console.log(job_costing_details);

      this.setState({
        temp_job_costing_details: job_costing_details,
        temp_expenses: expenses,
        expenses: expenses,
        total_cost,
        total_expense,
        id_location: job_costing.id_location,
        check: 0
      })

    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  handleSave = (job_costing_details, total_cost) => {
    //console.log(total_cost)
    this.setState({
      job_costing_details: job_costing_details,
      total_cost,
    });
  };

  clearJobCostingDetail = (job_costing_details, total_cost) => {
    //console.log(total_cost)
    this.setState({
      temp_job_costing_detail: null,
      job_costing_details: job_costing_details,
      total_cost,
      clear: false
    });
  };

  clearJobCostingDetails = (job_costing_details, total_cost) => {
    //console.log(total_cost)
    this.setState({
      temp_job_costing_details: [],
      job_costing_details: job_costing_details,
      total_cost,
      clear: false
    });
  };

  handleSaveExpense = (expenses, total_expense) => {
    this.setState({
      expenses: expenses,
      total_expense,
    });
  };

  clearExpense = (expenses, total_expense) => {

    this.setState({
      temp_expense: null,
      expenses: expenses,
      total_expense,
    });
  };

  clearExpenses = (expenses, total_expense) => {

    this.setState({
      temp_expenses: [],
      expenses: expenses,
      total_expense,
    });
  };

  handleChangeExpenseAccount = (e) => {
    const { accounts } = this.props.jobCosting;
    const [account] = accounts.filter(
      data => data.id_account === e
    );
    this.setState({
      temp_expense: account
    });

  }

  handleSelectLocation = id_location => {
    const { getItems } = this.props;
    this.setState({
      id_location: id_location,
      clear: this.state.id_location !== id_location ? true : false
    });

    getItems(id_location);
  };



  handleSelectItem = value => {
    const { items } = this.props.jobCosting;
    const { search, id_location } = this.state;
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      temp_job_costing_detail: items.filter(item => item.id_item === value)[0]
    });

    if (search) {
      getItems(id_location);
      this.setState({ search: null });
    }
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { id_location } = this.state;
    //console.log(value, id_location, getItems);
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_location) {
      getItems(id_location, value);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { messages } = this.props.jobCosting
      // console.log(err)
      // console.log(values)
      if (this.state.job_costing_details.length < 1) {
        notification("error", messages.item_required)
      }
      else if (!err) {
        values.transaction_date = values.transaction_date.format("YYYY-MM-DD HH:mm:ss");
        values.job_costing_details = this.state.job_costing_details;
        values.expenses = this.state.expenses;
        this.props.submit(values);
      }
    });
  };


  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { jobCosting, processJobCosting } = this.props;
    const { loading } = jobCosting;
    const { search } = this.state;

    const data = jobCosting.job_costing ? jobCosting.job_costing : [];

    let permissions = this.props.permissions;

    // const error = jobCosting.errorField ? jobCosting.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="jobCostings.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status < 3
                  ? data.status - 1
                  : data.status === 4
                    ? 2
                    : 3
              }
            >
              <Step title={getLanguage({id:"new"})} />
              <Step title={getLanguage({id:"processed"})} />
              <Step title={getLanguage({id:"finished"})} />
              <Step title={getLanguage({id:"cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="jobCostings.no" />}
                >
                  {getFieldDecorator("job_costing_no", {
                    initialValue: data.job_costing_no,

                  })(<Input
                    disabled={true}
                  />)}
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="purchaseOrders.transactionDate" />
                  }
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment(data.transaction_date, "YYYY-MM-DD HH:mm:ss"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      // onChange={this.handleChangeTransDate}
                      // onOpenChange={this.handleChangeTransDate}
                      disabled={
                        permissions.includes("Can Back Date") && data.status === 1
                          ? false
                          : true
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.jobCosting.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleSelectLocation}
                      disabled={data.status !== 1 ? true : false}
                      style={{ width: "100%" }}
                    >
                      {jobCosting.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="GLAccount" />}
                >
                  {getFieldDecorator("id_gl_account", {
                    // initialValue: data.id_gl_account,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.jobCosting.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={data.status !== 1 ? true : false}
                      style={{ width: "100%" }}
                    >

                      {jobCosting.accounts.map((data, key) => (
                        // data.id_account_type === "15" ||
                        // data.id_account_type === "16" ||
                        // data.id_account_type === "18" 
                        // ?
                        (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        )
                        //: null   
                      ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.description,
              })(<TextArea
                autosize={{ minRows: 4 }}
                disabled={data.status !== 1 ? true : false}
              />)}
            </Form.Item>

          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14, marginBottom: 0 }}>
          <IntlMessages id="jobCostings.subtitle.itemsExpenses" />
        </Divider>

        <Tabs defaultActiveKey="1">
          <TabPane tab={<IntlMessages id="jobCostings.tab.item" />}
            key="1"
          >
            {
              data.status === 1
                ?
                (
                  <Form.Item
                    {...formItemLayout}
                    //label={<IntlMessages id="jobCostings.item" />}
                    extra={<IntlMessages id="jobCostings.item.help" />}
                  // validateStatus={error.item_request_details ? "error" : null}
                  // help={
                  //   error.item_request_details ? error.item_request_details : null
                  // }
                  >
                    <Select
                      value={search ? search : []}
                      showSearch
                      allowClear
                      // optionFilterProp="children"
                      filterOption={false}
                      placeholder={
                        <IntlMessages id="selectItem" />
                      }
                      loading={loading}
                      notFoundContent={
                        loading ? (
                          <Spin size="small" />
                        ) : this.state.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                              <Empty
                                description={
                                  <IntlMessages id="itemRequests.item.noLocation" />
                                }
                              />
                            )
                      }
                      onSelect={this.handleSelectItem}
                      onSearch={this.handleSearchItem}
                    >
                      {jobCosting.items.map(data => (
                        <Option
                          key={data.id_item}
                          value={data.id_item}
                          disabled={data.current_qty
                            ? parseFloat(data.current_qty) > 0
                              ? false
                              : true
                            : true}
                        >
                          {data.item_code + " - " + data.item_name + " (Stock " + (data.current_qty ? parseFloat(data.current_qty) : 0) + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Form.Item>
                )
                : null
            }


            <EditableTableItem
              items={this.props.jobCosting.items}
              projects={this.props.jobCosting.projects}
              departments={this.props.jobCosting.departments}
              temp_job_costing_detail={this.state.temp_job_costing_detail}
              temp_job_costing_details={this.state.temp_job_costing_details}
              job_costing_details={this.state.job_costing_details}
              clearJobCostingDetail={this.clearJobCostingDetail}
              clearJobCostingDetails={this.clearJobCostingDetails}
              clear={this.state.clear}
              handleSave={this.handleSave}
              id_location={getFieldValue('id_location')}
              status={data.status}
            />

          </TabPane>
          <TabPane tab={<IntlMessages id="jobCostings.tab.expense" />}
            key="2"
          >
            {
              data.status === 1
                ?
                (
                  <Form.Item
                    {...formItemLayout}
                  //label={<IntlMessages id="GLAccount" />}
                  >
                    {getFieldDecorator("id_account_expense", {
                      // initialValue: data.id_gl_account,
                      // rules: [
                      //   {
                      //     required: true,
                      //     message: <IntlMessages id="required" />
                      //   }
                      // ]
                    })(
                      <Select
                        loading={this.props.jobCosting.loading}
                        showSearch
                        optionFilterProp="children"
                        placeholder={
                          <IntlMessages id="selectExpense" />
                        }
                        onChange={this.handleChangeExpenseAccount}
                      >

                        {jobCosting.accounts.map((data, key) => (
                          data.id_account_type === "15" ||
                            data.id_account_type === "16" ||
                            data.id_account_type === "18"
                            ?
                            (
                              <Option key={key} value={data.id_account}>
                                {data.account_code + " (" + data.account_name + ")"}
                              </Option>
                            )
                            : null
                        ))
                        }
                      </Select>
                    )}
                  </Form.Item>
                )
                : null
            }


            <EditableTableExpense
              accounts={this.props.jobCosting.accounts}
              temp_expense={this.state.temp_expense}
              temp_expenses={this.state.temp_expenses}
              expenses={this.state.expenses}
              clearExpense={this.clearExpense}
              clearExpenses={this.clearExpenses}
              handleSave={this.handleSaveExpense}
              id_location={getFieldValue('id_location')}
              status={data.status}
            />

          </TabPane>
        </Tabs>

        <Row>
          <Col span={12}></Col>
          <Col span={12} style={{ paddingLeft: "15px", paddingRight: "50px", marginTop: "25px" }}>
            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={15} style={{ marginTop: "5px", textAlign: "right", }}>
                <strong>
                  <IntlMessages id="totalCost" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right", }}>
                <span>
                  <NumberFormat value={this.state.total_cost} />
                </span>
              </Col>
              <Col span={1}></Col>
            </Row>
            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={14} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="totalExpense" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat value={this.state.total_expense} />
                </span>
              </Col>
              <Col span={2}></Col>
            </Row>
            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={14} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="total" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat value={parseFloat(this.state.total_cost + this.state.total_expense)} />
                </span>
              </Col>
              <Col span={2}></Col>
            </Row>
          </Col>
        </Row>


        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 1
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() =>
              processJobCosting(data.id_job_costing)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                  permissions.includes("Confirm Job Costing")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
                permissions.includes("Cancel Job Costing")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

        </Form.Item>
      </Form>
    );
  }
}

const WrappedJobCostingUpdateForm = Form.create({ name: "update_job_costing" })(JobCostingUpdateForm);

export default WrappedJobCostingUpdateForm;
