import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  state = {
    total_cost: 0
  };

  componentDidUpdate = prevProps => {
    if (prevProps.data !== this.props.data && this.props.data) {
      const data = this.props.data;
      let total_cost = 0;

      data.job_costing_details.map(x => {
        total_cost += parseFloat(x.base_qty) * parseFloat(x.cost_per_unit);
      });

      this.setState({
        total_cost
      });
    }
  };

  render() {
    let columnItem = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "right"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "qty" }),
        dataIndex: "base_qty",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "sidebar.unit" }),
        dataIndex: "base_unit_code",
        align: "center"
      },
      {
        title: getLanguage({ id: "cost" }),
        dataIndex: "cost_per_unit",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        align: "right",
        render: (text, row) => (
          <NumberFormat value={parseFloat(row.base_qty * row.cost_per_unit)} />
        )
      }
    ];

    let columnExpense = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "right"
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        dataIndex: "amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location_name
      },
      {
        label: <IntlMessages id="GLAccount" />,
        description: data.account_code + " (" + data.account_name + ")"
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="note" />,
        description: data.note
      }
    ];

    const addition = [];

    const total = [
      {
        label: <IntlMessages id="totalCost" />,
        amount: <NumberFormat value={this.state.total_cost} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalExpense" />,
        amount: <NumberFormat value={data.total_expense} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="total" />,
        amount: <NumberFormat value={data.total_amount} />
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.jobCosting" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.job_costing_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          {/* <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={3}>
                  <IntlMessages id="to" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={20}>
                  <strong>
                    {data.supplier ? data.supplier.supplier_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={3}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={20}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row> */}

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="jobCostings.tab.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={columnItem}
                dataSource={data.job_costing_details}
                rowKey={row => row.id_job_costing_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="jobCostings.tab.expense"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={columnExpense}
                dataSource={data.expenses}
                rowKey={row => row.id_job_costing_expense}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "job_costing",
              data.job_costing_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
