import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import JobCostingUpdateForm from "./updateForm";
import JobCostingDocUpdateForm from "./updateDocForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/jobCosting/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/inventories/job-costings";


class UpdateJobCosting extends Component {

  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editJobCosting } = this.props;
    const { id } = this.props.match.params;
    editJobCosting(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.jobCosting.error) {
      notification("error", this.props.jobCosting.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.jobCosting.success) {
      notification("success", this.props.jobCosting.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateJobCosting } = this.props;
    const { id } = this.props.match.params;

    updateJobCosting(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelJobCosting } = this.props;
    const { id } = this.props.match.params;

    cancelJobCosting(id, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { job_costing, loading } = this.props.jobCosting
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>  
        <Box>
          <PageHeader title={<IntlMessages id="jobCostings.update.title" />}
            style={{ margin:0 }}
          >
             <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={job_costing} auth={this.props.auth} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={job_costing ? job_costing.description : null}
          />
          
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} 
              key="1" 
            >
              {this.props.jobCosting.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <JobCostingUpdateForm
                  submit={this.handleSubmit}
                  jobCosting={this.props.jobCosting}
                  permissions={permissions}
                  processJobCosting={this.props.processJobCosting}
                  handleSubmitCancel={this.handleSubmitCancel}
                  getItems={this.props.getItems}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="jobCostings.tab.attachment" />} 
              key="2"
            >  
             {this.props.jobCosting.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <JobCostingDocUpdateForm
                  submit={this.handleSubmit}
                  jobCosting={this.props.jobCosting}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    jobCosting: state.jobCosting, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateJobCosting);
