import React, { Component } from "react";
import { Timeline, Modal } from "antd";
import { Link } from "react-router-dom";

import theme from "../../../../settings/themes/themedefault";
import IntlMessages from "../../../../components/utility/intlMessages";

// const redirectPR = "/dashboard/purchases/purchase-receipts";

class HistoryPurchaseOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [],
      desc: null
    };
  }

  componentDidUpdate(prevProps) {
    const { description } = this.props;
    const { desc } = this.state;
    if (prevProps.description !== desc && description) {
      const spliteds = description.split("|").reverse();

      this.setState({
        history: [...spliteds],
        desc: description
      });
    }
  }

  render() {
    const { history } = this.state;

    return (
      <Modal
        title={<IntlMessages id="itemRequests.history" />}
        centered
        visible={this.props.visible}
        footer={false}
        width="50%"
        onCancel={this.props.handleModalHistory}
      >
        <Timeline mode="alternate">
          {history.map((data, key) => {
            if (data.indexOf("Created") >= 0) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[0]}>
                  {data}
                </Timeline.Item>
              );
            }
            else if (data.indexOf("Updated") >= 0) {
              const info = data.indexOf("{") >= 0 ? data.slice(0, data.indexOf("{")) : data;
              const obj = JSON.parse(data.slice(data.indexOf("{")));
              return (
                <Timeline.Item key={key} color={theme.palette.status[0]}>
                  {info}
                  <br />
                  {obj.changes ? (
                    <div>
                      <span>Changes: </span>
                      {obj.changes.map((data, key) => {
                        return (
                          <div key={key}>
                            <span>{`${key + 1}. ${data}`}</span>
                            <br />
                          </div>
                        );
                      })}
                    </div>
                  ) : null}
                </Timeline.Item>
              );
            }
            else if (
              data.indexOf("Processed") >= 0
            ) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[4]}>
                  {data}
                </Timeline.Item>
              );
            }
            // else if (data.indexOf("Received") >= 0) {
            //   const info = data.slice(0, data.indexOf("{"));
            //   const pr = JSON.parse(data.slice(data.indexOf("{")));

            //   return (
            //     <Timeline.Item key={key} color={theme.palette.status[2]}>
            //       {info}
            //       <br />
            //       <span>PR: </span>
            //       <Link to={`${redirectPR}/update/${pr.id}`}>
            //         {pr.transaction_no}
            //       </Link>
            //     </Timeline.Item>
            //   );
            // }
            else if (data.indexOf("Finished") >= 0) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[1]}>
                  {data}
                </Timeline.Item>
              );
            }
            else if (data.indexOf("Cancelled") >= 0) {
              const info = data.slice(0, data.indexOf("{"));
              const reason = JSON.parse(data.slice(data.indexOf("{")));

              return (
                <Timeline.Item key={key} color={theme.palette.status[3]}>
                  {info}
                  <br />
                  <span>Reason: {reason.cancelled_reason}</span>
                </Timeline.Item>
              );
            }
            else if (data.indexOf("Uploaded") >= 0) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[6]}>
                  {data}
                </Timeline.Item>
              );
            }
            else if (data.indexOf("Deleted") >= 0) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[7]}>
                  {data}
                </Timeline.Item>
              );
            }
          })}
        </Timeline>
      </Modal>
    );
  }
}

export default HistoryPurchaseOrder;
